<template>
  <div class="card-container loginClass">
      <v-container>
      <div class="signup-form-group row">
        <div class="col-lg-6 col-md-6 col-sm-8 mx-auto">
          <!--   Form Start-->
          <div class="card flex-grid signup">
            <main><h2><strong>   </strong></h2></main>

            <form class="form-group my-3" @submit.prevent="handleRegister">
              <!-- nickname  Form -->
              <v-text-field type="text" label="* " hide-details="auto" :rules="Message"
                            v-model="user.nickname" id="nickname-signup" required />
              <!-- Email  Form &&    -->
              <div class="row px-3 my-1" style="align-items: baseline">
                <v-text-field type="email" label="* Email" hide-details="auto" :rules="Message"
                              v-model="user.email" id="email-signup" required />

              </div>
              <!--         ,   Form       -->
              <div v-if="cert_key" class="d-flex my-3" style="align-items: baseline">

                <v-text-field type="text" label="* " hide-details="auto" :rules="Message"
                              v-model="confirm_key" id="cert-key" required />

                <v-btn rounded @click="confirmKey" elevation="5" color="primary"
                       :disabled="validated_key"> </v-btn>
              </div>
              <div v-else class="my-3 py-3" style="align-items: baseline">
                <v-btn rounded elevation="3" class="row-2" color="primary"
                       @click="emailDuplication"> </v-btn>
              </div>
              <div class="row px-3">
                <!-- Password  Form -->
                <v-text-field type="password" label="* " hide-details="auto" :rules="Message"
                              v-model="user.password" id="password-signup" required
                              oninput="this.value = this.value.replace(/[-|-|-]/g, '' )" />
                <!-- PasswordConfirm  Form -->
                <hr>
                <v-text-field type="password" label="*  " hide-details="auto"
                              :rules="errorMessages" v-model="user.passwordConfirm" id="passwordConfirm-signup" required
                              oninput="this.value = this.value.replace(/[-|-|-]/g, '' )"/>
              </div>
              <!--    -->
              <br>
              <h5><strong>*   .</strong></h5>
              <div class="field text-center" id="submit-signup-form">
                <v-btn elevation="11" type="submit" color="primary"></v-btn>
                <br>
                <a role="link" :href="'Login'">  ?</a>
              </div>
            </form>
            <!--  Form -->
            <v-snackbar color="pink" v-model="submitted">
              <template>{{ snackMessage }}</template>
            </v-snackbar>
          </div>
            <!--   Form End -->
        </div>
      </div>
      </v-container>
  </div>
</template>

<script>
import axios from "axios"
const SERVER_URL = process.env.VUE_APP_SERVER_URL

export default {
 name: 'Signup',
 data() {
   return{
     user: {gender: 0},
     submitted: false,
     cert_key: false,
     confirm_key: "",
     validated_key: false,
     snackMessage: '',
     Message: [ value => !!value || ' .' ],
     errorMessages: [
       value => !!value || ' .',
       value => (value === this.user.password) || " ."]
  }
 },
 computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn
    },
  },
  mounted() {
    if (this.loggedIn){
      this.$router.push({name: "Login"})
    }
  },
  methods: {
   //   
   handleRegister() {
     if (this.validated_key) {
       this.$store.dispatch("auth/register", this.user)
         .then(() => {
           alert(`${this.user.nickname}  `)
           setTimeout(() => this.$router.push({name: "Login"}))
         })
         .catch(() => {
           this.submitted = true
           this.snackNum = '     .'
           setTimeout(() => this.submitted = false, 1000)
         })
     }
    },
    //      
    emailDuplication() {
      this.confirm_key = ''
      this.validated_key = false
      if (this.user.email && this.user.email.indexOf('@') !== -1) {
        this.cert_key = true
        axios({
          baseURL: SERVER_URL,
          url: '/email/key',
          method: 'POST',
          data: {'email': this.user.email},
        })
          .then(() => {
            this.submitted = true
            this.snackMessage = " .\n   ."
            setTimeout(() => this.submitted = false, 1000)
          })
          .catch(() => {
            this.submitted = true
            this.snackMessage = '  .'
            setTimeout(() => this.submitted = false, 1000)
            this.cert_key = false
          })
      } else {
        this.submitted = true
        this.snackMessage = ' '
        setTimeout(() => this.submitted = false, 1000)
      }
    },
    //    
    confirmKey() {
     axios({
       baseURL: SERVER_URL,
       url: '/email/key-check',
       method: 'POST',
       data: {
         'email': this.user.email,
         'key': this.confirm_key },
     })
      .then(() => {
        this.submitted = true
        this.snackMessage = '.'
        setTimeout(() => this.submitted = false, 1000)
        this.validated_key = true
      })
      .catch(() => {
        this.submitted = true
        this.snackMessage = ' .'
        setTimeout(() => this.submitted = false, 1000)
        this.validated_key = false
      })
   }},
  }
</script>

<style>
p {
  line-height: 1rem;
}

.card {
  padding: 3rem;
  border-radius: 10%;
}

.form-group > input {
  margin-bottom: 20px;
  /* background: black; */
}

.field {
  margin-top: 2rem;
}

.field > button {
  margin-bottom: 1rem;
}

.field > button:hover {
  background: rgb(80, 87, 80);
  color: #000000;
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
}

.hr-sect {
	display: flex;
	flex-basis: 100%;
	align-items: center;
	color: rgba(0, 0, 0, 0.35);
	font-size: 12px;
	margin: 8px 0;
}
.hr-sect::before,
.hr-sect::after {
	content: "";
	flex-grow: 1;
	background: rgba(0, 0, 0, 0.35);
	height: 1px;
	font-size: 0;
	line-height: 0;
	margin: 0 16px;
}


</style>