<template>
  <div class="col-12 col-md-10 offset-md-1">
    <v-form
      ref="form"
      v-model="valid"
      lazy-validation
    >
      <v-text-field
        v-model="title"
        :rules="titleRules"
        label=" "
        required
      ></v-text-field>

      <v-img v-if="!!book" :src="book.thumbnailUrl" max-height="540px" contain></v-img>

      <v-autocomplete
        v-model="book"
        clearable
        hide-no-data
        return-object
        :items="bookItems"
        :loading="bookLoading"
        :search-input.sync="bookSearch"
        item-text="shortName"
        item-value="id"
        label="">
      </v-autocomplete>
      <!-- Autocomplete   DB              -->

      <v-select
        v-model="max_members"
        :items="max_member_items"
        :rules="[v => !!v || ' .']"
        label=" "
        required
      ></v-select>

      <v-text-field type="datetime-local" :value="date_time" v-model="date_time" :min="date_time_now" required>
      </v-text-field>


      <v-text-field
        v-model="tags"
        :counter="10"
        label=""
        required
      ></v-text-field>

      <v-textarea
        v-model="description"
        label=""
        required
      ></v-textarea>

      <!--    -->

      <v-text-field
        v-model="conf_question"
        label=" "
      ></v-text-field>

      <v-text-field
        v-model="conf_answer"
        label=" "
      ></v-text-field>

      <v-btn
        :disabled="!valid"
        color="success"
        class="mr-4"
        @click="validate"
      >
         
      </v-btn>

      <v-btn
        color="error"
        class="mr-4"
        @click="reset"
      >
         
      </v-btn>

      <v-btn
        color="warning"
        @click="$router.go(-1)"
      >
        
      </v-btn>
    </v-form>
  </div>
</template>

<script>
const SERVER_URL = process.env.VUE_APP_SERVER_URL

import _ from 'lodash'
import axios from 'axios'
import moment from 'moment'
import { mapState } from 'vuex'
// import authheader from "@/services/auth-header"
export default {
  name: 'CreateConference',
  data: () => ({
    valid: true,
    title: '',
    titleRules: [
      v => !!v || '    .',
      v => (v && v.length < 30) || '   .',
    ],
    
    max_members: null,
    max_member_items: _.range(2, 31),

    date_time: moment().add(1, 'hours').format('YYYY-MM-DDTHH:00'),
    date_time_now: moment().format('YYYY-MM-DDTHH:mm'),


    date_menu: false,
    dateRules: [
      v => !!v || ' .',
      // v => v - this.nowDate < 0 || '   .',
    ],
    conf_question: '',
    conf_answer: '',

    // conf_answerRules: [
    //   v => !(!!this.conf_question&& !v) || '       .',
    // ],


    description: '',
    thumbnailUrl: '',

    book: {},
    bookSearch: null,
    bookLoading: false,
    nameLimit: 20,
    bookEntries: [],
    tags: '',

  }),

  methods: {
    validate () {
      const validate = this.$refs.form.validate()
      if (validate) {
        this.createConference()
      }
    },
    reset () {
      this.$refs.form.reset()
    },
    resetValidation () {
      this.$refs.form.resetValidation()
    },


    createConference: function () {
      
      //   
      if (!this.$store.state.auth.status.loggedIn) {
        this.$router.push({name: 'Login'})
        return
      }
      // date_time: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 14) + "00",
      
      const endTime = moment(this.date_time, 'YYYY-MM-DDTHH:mm').add(1, 'hours').format('YYYY-MM-DDTHH:mm')
      
      
      console.log(this.date_time)
      const conference = {
        userId: this.$store.state.auth.user.id,
        title: this.title,
        bookId: this.book.id,
        callEndTime: endTime,
        callStartTime: this.date_time,
        description: this.description,
        maxMembers: this.max_members,
        password: this.conf_answer,
        question: this.conf_question,
        tags: this.tags,
        thumbnailUrl: this.thumbnailUrl ? this.thumbnailUrl : this.book.thumbnailUrl,
      }

      console.log(conference)
      // console.log(authheader())
      //    
      axios({
        method: 'POST',
        baseURL: SERVER_URL,
        url: '/conference',
        headers: {
          'X-AUTH-TOKEN': this.$store.state.auth.user.token
        },
        // headers: authheader(),
        data: conference,
      })
      .then(response => {
        //  
        // 1.    (Snackbar? Modal?) 
        
        console.log(response)
        // 2.    
        this.$router.push({name: 'Profile'})
      })
      .catch(error => {
        console.log(error)
        
      })
    },

    fetchEntriesDebounced: function () {
      clearTimeout(this._timerId)
      this._timerId = setTimeout(() => {
        axios({
        method: 'GET',
        baseURL: SERVER_URL,
        url: `/search/book?book_name=${this.bookSearch}&page=0&size=10`,
      })
      .then(response => {
        console.log('searching')
        this.bookEntries = response.data.content
      })
      .catch(error => {
        console.log(error)
      })
      .finally(() => (this.bookLoading = false))
        
      }, 500)
      },
  },

  computed: {
    user: function () {
      return this.$store.state.auth.status.user
    },

    bookItems: function () {
      return this.bookEntries.map(entry => {
        const id = entry.id
        const shortName = entry.name.length > this.nameLimit
          ? entry.name.slice(0, this.nameLimit) + '...'
          : entry.name
        
        return Object.assign({}, entry, {id: id, shortName: shortName})
      })
    },
    ...mapState(['auth'])
  },

  watch: {
    bookSearch () {
      if (this.bookLoading) return
      this.bookLoading = true

      this.fetchEntriesDebounced()
    },
  },

  created: function () {
    if (!this.auth.status.loggedIn) {
      alert('  .')
      this.$router.push({name: 'Home'})
    }
  },

  mounted: function () {
    if (this.$route.query.bookId) {
      axios({
        method: 'GET',
        baseURL: SERVER_URL,
        url: `books/${this.$route.query.bookId}`,
      })
      .then(response => {
        this.book = response.data
        this.bookEntries = [ this.book ]
      })
      .catch(error => {
        console.log(error)
      })
    } else {
      axios({
        method: 'GET',
        baseURL: SERVER_URL,
        url: `/search/book?book_name=&page=0&size=10`,
      })
      .then(response => {
        console.log('searching')
        this.bookEntries = response.data.content
      })
      .catch(error => {
        console.log(error)
      })
      .finally(() => (this.bookLoading = false))
    }
  }
  
}
</script>

