<template>
  <!--  -->
  <div>
    <v-text-field
      flat solo rounded
      prepend-inner-icon="mdi-magnify"
      hide-details="true"
      label=""
      v-model="keyword"
      @keyup.enter="searchKeyword(keyword)"
    />  
  </div>
</template>

<script>
export default {
  name: "SearchBar",
  
  data: function () {
    return {
      keyword: '',
    }
  },
  
  methods: {
    // 
    // Navbar      ,
    //       .
    searchKeyword: function (keyword) {
      console.log(keyword)
      this.$router.push({
        name: 'Search',
        params: {'type': 'conference'},
        query: {
          'keyword': String(keyword),
          'page': parseInt(1)
        }
      })
    },
  },

}
</script>

<style>
.v-text-field.v-text-field--solo .v-input__control {
  min-height: 42px !important;
}
.v-input .v-input--hide-details {
  width: 2rem !important;

}

</style>